.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  cursor: pointer;
}
.container .wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  transform: translate(-50%, -50%);
}

h1,
h2 {
  margin-bottom: 3rem;
  color: coral;
  animation: fadeIn 0.25s ease-in forwards, slideIn_down 0.4s ease-out forwards;
}

.card {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 12rem;
  height: 14rem;
  margin: 1.5rem;
  text-align: center;
  background: none;
  border: 0.1rem solid;
  border-color: yellow;
  border-radius: 0.5rem;
  animation: fadeIn 1s ease-in forwards, slideIn_down 1s ease-out forwards;
}

.btn__text {
  font-size: 1.4rem;
  font-weight: 300;
  color: whitesmoke;
  text-align: center;
  animation: fadeIn 1s ease-in forwards, slideIn_up 1s ease-out forwards;
}